var React = require('react');
var ReactDOM = require('react-dom');
var bootstrap = require('react-bootstrap');

var formStyle = {
	marginTop: '20%'
};

var buttonStyle = {
	width: '100%'
};

var LoginBox = React.createClass({
	render: function(){
		return(
			<bootstrap.Grid>
				<LoginForm ></LoginForm>
			</bootstrap.Grid>
		);
	}
});

var LoginForm = React.createClass({
	render: function(){
		return(
			<form method="post" action="#" style={formStyle}>
			  <bootstrap.Row className="show-grid">
					<bootstrap.Col xs={8} xsOffset={2} md={6} mdOffset={3}>
						<h3><center><strong>登录</strong></center></h3>
						<hr/>
						<bootstrap.Input type="text" name="username" placeholder="用户名" required />
						<bootstrap.Input type="password" name="password" placeholder="密码" required />
						<bootstrap.Button bsStyle="primary" style={buttonStyle} type="submit">登录</bootstrap.Button>
						<h5><center><a href="#">立即注册</a></center></h5>
					</bootstrap.Col>	
				</bootstrap.Row>	
			</form>
		);
	}
});

ReactDOM.render(<LoginBox />, document.getElementById("content"));


